import React, { Component } from 'react'
import SwiperCore, { Autoplay,EffectFade} from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import {Link} from 'react-router-dom'
import 'swiper/swiper-bundle.css';
SwiperCore.use([Autoplay,EffectFade]);
export default class Trust extends Component {
Scroll = () => {
if(window.innerWidth > 900){
  return 4;
}
else{
  return 2;
}
};
  render() {
    return (
<Swiper effect="fade"
      spaceBetween={30} 
      slidesPerView={1}
      loop={true}
      autoplay={{
        delay:5000,
        disableOnInteraction:false
    }}
    >
      <SwiperSlide>
          <div className='slider'>
<div className='cta-txt-btn'>
<p className='Slider-txt'>Lorem Ipsum is simply dummy text of the printing and typesetting</p>
              <Link to='' className='cta-btn'>Contact us</Link>
</div>
         
          </div>
      </SwiperSlide>
      <SwiperSlide>
          <div className='slider'>
<div className='cta-txt-btn'>
<p className='Slider-txt'>Porem Lpsum is Jimply dummy Aext of the printing and typesetting</p>
              <Link to='' className='cta-btn'>Contact us</Link>
</div>
         
          </div>
      </SwiperSlide>
    </Swiper>

    )
  }
}